@import url(https://fonts.googleapis.com/css?family=Coiny);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca|Livvic&display=swap);
@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.hidden {
  display: none;
}

.quizEndDiv {
  position: relative;
  min-height: 50vh;
}

/* default colour theme */
html {
  --main-neutral-very-light: white;
	--main-neutral-light: #faf9f2;
  --main-neutral-med-light: #f5f1e7;
  --main-neutral-med: #73736e;
	--main-neutral-dark: #393939;
  --main-neutral-vary_dark: #1d1c1c;
  --main-neutral-dark_rgba: rgba(57, 57, 57, 0.3);

	--main-green-dark: #3d442f;
	--main-green-med: #82955a;
	--main-green-light: #a0b27e;
  --main-green-very-light: #e1e6d7;

  --main-blue-dark: #017597;
	--main-blue-med: #2898a5;
	--main-blue-light: #65e1f7;
  --main-blue-very-light: #c6e5e5;

  --main-violet-light: #B694DA;
  --main-violet2-very-light: #d9b5e6;

  --main-beige-very-light: #ede1ce;
}


/* GENERAL ********************************************************************/

* {
  color: #393939;
  color: var(--main-neutral-dark);
  font-family: 'Livvic', sans-serif;
  letter-spacing: 0.03em;
  /* font-family: 'Lexend Deca', sans-serif; */
}

body {
  background-color: #faf9f2;
  background-color: var(--main-neutral-light);
  min-height: 100vh;
}

#root {
    min-height: calc(100vh - 171px);
    position: relative;
    padding-bottom: 171px;
}

a {
  cursor: pointer;
}

button,
input[type="submit"],
input[type="file"] {
  padding: 13px 20px;
}

input[type="checkbox"] {
  margin: 10px;
}

input[type="file"] {
  box-sizing: border-box;
  border: 2px solid #f5f1e7;
  border: 2px solid var(--main-neutral-med-light);

}

input {
  padding: 10px;
  margin: 5px;
  display: block;
  box-sizing: border-box;
  border: 2px solid #f5f1e7;
  border: 2px solid var(--main-neutral-med-light);
  border-radius: 4px;
  font-size: 1em;
}

input:focus {
  outline: none;
  border: 2px solid rgba(57, 57, 57, 0.3);
  border: 2px solid var(--main-neutral-dark_rgba);
  box-shadow: 0 0 10px rgba(57, 57, 57, 0.3);
  box-shadow: 0 0 10px var(--main-neutral-dark_rgba);
}

textarea {
  border: 2px solid #f5f1e7;
  border: 2px solid var(--main-neutral-med-light);
  width: 400px;
  height: 150px;
  display: block;
}

h4 {
  margin: 0;
  text-shadow: 2px 2px #e1e6d7;
  text-shadow: 2px 2px var(--main-green-very-light);
}

.wrapper {
  margin: 0 auto;
  max-width: 900px;
}

/* HEADER *********************************************************************/

.nav {
  display: grid;
  grid-template-columns: auto auto;
  padding: 0 30px;
}

.nav .links {
  text-align: right;
  display: inline-block;
}

.nav a {
  display: inline-block;
  text-decoration: none;
  padding: 20px;
  color: #393939;
  color: var(--main-neutral-dark);
}

.nav a:hover {
  color: #82955a;
  color: var(--main-green-med)
}

.logo {
  max-height: 1em;
  max-width: 1em;
  display: inline;
  vertical-align: middle;
}

.nav h1 {
  display: inline-block;
  cursor: pointer;
}

.nav h1 .hover {
  display: none;
}

.nav h1:hover {
  color: #82955a;
  color: var(--main-green-med);
  cursor: pointer;
}

.nav h1:hover .nohover {
  display: none;
}

.nav h1:hover .hover {
  display: inline-block;
}

a.headerTitle {
  padding: 0;
  cursor: pointer;
}

.featureImage {
  margin: 0 auto;
  max-width: 500px;
  width: 100%;
  display: block;
}

/* FOOTER **********************************************************************/

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  margin-top: 20px;
  min-height: 171px;
  color: #faf9f2;
  color: var(--main-neutral-light);
  background-image: url(/greenify-client/static/media/dirt_short.d8f7b140.png);
  background-repeat: repeat-x;
}

.footerPos {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 20px);

}

.footer span {
  padding: 30px;
  padding-top: 100px;
  box-sizing: border-box;
  display: inline-block;
  color: #faf9f2;
  color: var(--main-neutral-light);
}

/* ALL PLANTS *****************************************************************/

.allPlantsGrid {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 15px;

}
.allPlantsGrid p {
  text-align: left;
}
.allPlantName {
    display: block;
    padding-bottom: 15px;
}

.allPlantName p {
    display: block;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
}


.allPlantsShow {
  border-radius: 4px;
  display: block;
  object-fit: cover;
  max-height: 220px;
  box-shadow: 0 0 20px rgba(57, 57, 57, 0.3);
  box-shadow: 0 0 20px var(--main-neutral-dark_rgba);

}

/* PLANT PROFILE **************************************************************/

.plantProfileGrid {
  display: grid;
  grid-template-columns: repeat(12, auto);
  grid-column-gap: 10px;
}

.plantProfileTitle {
  text-align: center;
}

.plantProfileBold {
  font-weight: bold;
  color: #3d442f;
  color: var(--main-green-dark);
}

.plantProfileImage {
  grid-column-start: 1;
  grid-column-end: 4;
}

.plantProfileStats {
  grid-column-start: 5;
  grid-column-end: 12;
}

.plantProfileBookingDates {
  text-align: center;
  grid-column-start: 1;
  grid-column-end: 12;
  grid-row-start: 2;
  grid-row-end: 3;

}

.plantProfileBookingDates form,
.plantProfileBookingDates input {
  display: block;
  margin: 0 auto;
}

.plantProfileBookingGrid {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

.plantProfileImage img {
  border-radius: 4px;
  width: 100%;
  min-width: 250px;
}

.plantProfileButtonArea {
  text-align: center;
  padding-top: 30px;
}

.bookedDates {
  background-color: #faf9f2;
  background-color: var(--main-neutral-light);
  border-radius: 4px;
}

.bookedDates:hover {
  background-color: #faf9f2;
  background-color: var(--main-neutral-light);
  border-radius: 4px;
}


.comment {
  width: 100%;
  display: block;
  background-color: white;
  background-color: var(--main-neutral-very-light);
  border-radius: 5px;
  border: 2px solid #f5f1e7;
  border: 2px solid var(--main-neutral-med-light);
  padding: 20px;
  box-sizing: border-box;
}

.commentTime {
  font-style: italic;
  color: #73736e;
  color: var(--main-neutral-med);
  font-size: 0.8em;
  vertical-align: baseline;
}

.ratingImg {
  max-width: 20px;
  max-height: 20px;
}

/* All Forms ******************************************************************/

.centerForm {
  text-align: center;
}

.centerForm input,
.centerForm textarea,
.centerForm img {
  margin: 0 auto;
}

input[type="checkbox"] {
  margin: 10px auto;
}

select {
  display: block;
}

/* PROFILE ********************************************************************/

.profileGrid {
  display: grid;
  grid-template-columns: repeat(6, auto);
}

.profileGrid div {
  text-align: center;
}

.profileGrid img {
  margin: 0 auto;
}

.userProfile {
  text-align: center;
  margin: 0 auto;
}

.profile {
  max-width: 100px;
  max-height: 100px;
  margin: 0 auto;
  display: block;
}

.profileSmall {
  max-width: 50px;
  max-height: 50px;
  display: block;
}

.title {
  font-family: 'Satisfy', cursive;
  font-size: 50px;
  color: #82955a;
  color: var(--main-green-med);
  margin-bottom: 20px;
  padding: 11px;
  text-align: center;
  font-weight: 400;

}

.question {
  color: #806000;
  margin-bottom: 10px;
}

.answerBtn{
  background-color: #e6ccb3;
  margin-left: 20px;
  margin-bottom: 20px;
  font-size: 20px;
}

button:hover {
opacity: 0.6;
}

.avatar {
    border-radius: 25px;
    width: 1.8em;
    height: 1.8em;
    padding-right: 5px;
    vertical-align: middle;
}

.backgroundimg {
  background-color: #e1e6d7;
  background-color: var(--main-green-very-light);
  box-shadow: 0 0 20px rgba(57, 57, 57, 0.3);
  box-shadow: 0 0 20px var(--main-neutral-dark_rgba);
}

.finalImg {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    border-radius: 50px;
}

.userAssociationGrid {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

.UserPlantBook,
.UserPlantOwn {
  margin: 10px;
  max-width: calc(30% - 20px);
  text-align: left;
}

.UserPlantBook img,
.UserPlantOwn img {
  margin: 0 auto;
  width: 100%;
  border-radius: 10px;
}

.quiztest {
  color: #82955a;
  color: var(--main-green-med);
    font-family: 'Satisfy', cursive;
    font-size: 35px;
    text-align: center;
    font-weight: 400;
}

.newPlant {
  color: #e1e6d7;
  color: var(--main-green-very-light);
  background-color: #82955a;
  background-color: var(--main-green-med);
  border-radius: 4px;
  margin: 0 5px;
  padding: 4px 2px;
  text-transform: uppercase;
  font-weight: bold;
}
.center-align {
  text-align: center;
}
.allPlantsGrid h4 {
  display: inline;
}

/* RESPONSIVE *****************************************************************/

@media only screen and (max-width: 900px) {

  .allPlantsGrid {
    grid-template-columns: auto auto;
  }

  .plantProfileImage img {
    min-width: 200px;
  }
}

@media only screen and (max-width: 700px) {
  .nav {
    grid-template-columns: auto;
  }

  .headerTitle,
  .nav .links {
    text-align: center;
  }

  .plantProfileImage img {
    border-radius: 4px;
    min-width: 150px;
  }
}


@media only screen and (max-width: 600px) {
  .allPlantsGrid {
    grid-template-columns: auto;
  }

  .greenify-text,
  .featureImage {
    -webkit-transform: scale(0.8, 0.8);
            transform: scale(0.8, 0.8);
  }

  .plant {
    -webkit-transform: scale(0.5, 0.5);
            transform: scale(0.5, 0.5);
  }

  .footer {
    min-height: 137px;
  }

  .footer span {
    padding-top: 80px;
  }

  .plantProfileImage img {
    border-radius: 4px;
    min-width: 100px;
  }

}

@media only screen and (max-width: 500px) {
  .greenify-text,
  .featureImage {
    -webkit-transform: scale(0.6, 0.6);
            transform: scale(0.6, 0.6);
  }
}
