/* Paulinas awesome header! ****************************************************/

.greenify-text {
	font-family: 'Coiny', cursive;
	-webkit-background-clip: text;
  	-webkit-text-fill-color: transparent;
	background-image: url(https://images.unsplash.com/photo-1454721738233-1bda0441023a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60);
	background-size: auto;
	background-attachment: unset;
	background-position: 0%;
  margin: 0;
	font-size: 107px;
	-webkit-animation: greenify-moving infinite 220s;
	        animation: greenify-moving infinite 220s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: running;
	        animation-play-state: running;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
	text-align: center;
}
@-webkit-keyframes greenify-moving {
	0% {
		background-position: 0%;
	}
	50% {
		background-position: 100%;
	}
	100% {
		background-position: 0%;
	}
}
@keyframes greenify-moving {
	0% {
		background-position: 0%;
	}
	50% {
		background-position: 100%;
	}
	100% {
		background-position: 0%;
	}
}
.attribute {
	position: relative;
	font-size: 26px;
	text-align: center;
}

/* Paulinas awesome plant! ****************************************************/

.plantContainer {
  width: 100%;
  height: 100%;
}

.plant {
    position: absolute;
    bottom: -25px;
    right: 100px;
    width: 50px;
    height: 20px;
    -webkit-transform: scale(0.7, 0.7);
            transform: scale(0.7, 0.7);
}

/*plant*/
.branch {
  -webkit-animation:sway 5s infinite linear alternate;
          animation:sway 5s infinite linear alternate;
  border-bottom:none;
  border-left:solid 6px #060;
  border-right:none;
  border-top:none;
  border-top-left-radius:100%;
  bottom:0;
  height:250px;
  left:25%;
  position:absolute;
  -webkit-transform-origin:bottom;
          transform-origin:bottom;
  width:50px;
}

.branch:nth-child(2) {
  border-width:6px;
  height:300px;
}

.branch:nth-child(3) {
  border-color:#070;
  border-width:12px;
  height:400px;
}

.branch:nth-child(4) {
  animation:sway 5s infinite linear alternate-reverse;
  border-left:none;
  border-right:solid 12px #070;
  border-top-right-radius:100%;
  height:350px;
  left:auto;
  right:74%;
}

.branch:nth-child(5) {
  animation:sway 5s infinite linear alternate-reverse;
  border-left:none;
  border-right:solid 9px #060;
  border-top-right-radius:100%;
  height:250px;
  left:auto;
  right:75%;
}

.branch:nth-child(6) {
  animation:sway 5s infinite linear alternate-reverse;
  border-left:none;
  border-right:solid 6px #050;
  border-top-right-radius:100%;
  height:150px;
  left:auto;
  right:75%;
}

@-webkit-keyframes sway {
  0% {
    width:50px;
  }
  100% {
    width:200px;
  }
}

@keyframes sway {
  0% {
    width:50px;
  }
  100% {
    width:200px;
  }
}




.container{
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
}
.card-container {
  display: inline-block;
  margin: 0 auto;

  /* better flipping effect */
  -webkit-perspective: 900px;
          perspective: 900px;
  text-align: center;
}
.card {
  position: relative;
  width: 340px;
  height: 280px;
  /* it makes the card flip slower! Nicer effect!*/
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
  /* without it the cards would not be displaying the back! And the text would be backwards after flipping */
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.front, .back {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
border-radius: 5px;
color: white;
box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3), 0 17px 17px 0 rgba(0, 0, 0, 0.15);
/* this is essential to have the backflip work */
-webkit-backface-visibility: hidden;
        backface-visibility: hidden;
}
.front {
  background-color: var(--main-green-light);
  font-size: 12px;
}
.card-img{
  background: black;
  width: 100%;
  height: 210px;
  /*THank you Lucy for showing me this! It clips the img and fit into a specified area */
  overflow:hidden;
}
.card-body{
    width: 100%;
    height: 150px;
  }
  .card-footer{
    font-size: 16px;
  }

.back {
  background: var(--main-violet-light);
  font-size: 14px;

  }


.pelBack{
  display: block;
  padding-top: 40px;
  font-weight: bold;
  padding-left: 10px;

}

  .flipbutton {
    background-color: var(--main-violet2-very-light);
    border-radius: 4px;
    border: solid 1px var(--main-green-dark);
    color: var(--main-green-dark);
    padding: .5em 1em;
    height: 50px;
    width: 200px;
}

/* we need to have both rotateY and rotateX - otherwise it would not work */
.card-container:hover .card {
-webkit-transform: rotateY(180deg);
        transform: rotateY(180deg);
}
.back {
-webkit-transform: rotateY(180deg);
        transform: rotateY(180deg);
}

.em{
  padding-left: 120px;
}

